/**
 * @module UTF16
 * @author nuintun
 */
/**
 * @function UTF16
 * @param {string} text
 * @returns {number[]}
 */
function UTF16(text) {
    var bytes = [];
    var length = text.length;
    for (var i = 0; i < length; i++) {
        bytes.push(text.charCodeAt(i));
    }
    return bytes;
}

export { UTF16 };
